const convertTeam = str => {
	// ,     "TeamN",  N - 
	const match = str.match(/^Team(\d+)$/);

	if (match) {
		//    
		const teamNumber = match[1];

		//      "Tour N"
		const tourString = `Tour ${teamNumber}.`;

		return tourString;
	} else {
		//    ,   
		return str;
	}
};

export default convertTeam;
